.login-pg {
    width: 100%;
    height: 100vh;
    padding-top: 70px;
    background: url(footer.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    align-items: center;
}

.login-container {
    width: 400px;
    height: 450px;
    background: white;
    margin: auto;
    padding: 20px 30px;
    border: 1px solid black;
    border-radius: 20px;
    background: transparent;
    backdrop-filter: blur(20px);
}

.login-container h1 {
    margin: 10px 0;
}

.login-input {
    display: flex;
    flex-direction: column;
    gap: 30px;
    margin-top: 15px;
}

.login-input input {
    height: 40px;
    width: 100%;
    padding-left: 20px;
    border: 2px solid black;
    outline: none;
    color: gray;
    font-size: 16px;
}
.login-container p{
    padding-top: 10px;
}
.login-container button {
    margin: 20px auto;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 300px;
    height: 35px;
    font-size: 16px;
    font-weight: 600;
    border: 2px solid black;
    background-color: red;
    border-radius: 40px;
}
.login-have span{
    color: rgb(0, 255, 191);
    cursor: pointer;
}
.login-agree{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 15px;
    font-weight: 300;
    text-decoration: underline;
}
.login-agree input{
    margin-top: -2px;
}
.login-agree p{
    padding-left: 2px;
}