.explore {
    margin-top: 165px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
}

.explore_top {
    display: grid;
    justify-content: center;
}
.explore_top h5{
    color: cyan;
}

.input_box {
    display: grid;
    grid-template-columns: auto auto auto auto;
}

.input_box input,
button {
    width: 200px;
    height: 35px;
    border-radius: 30px;
    padding: 10px;
}
.input_box button{
    padding: 0;
    background-color: cyan;
}

.lists {
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.lists li {
    list-style: none;
    margin-left: 20px;
    padding:0  5px;
}
.lists li:hover{
    border: 1px solid white;
    border-radius: 20px;
}
.photos{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 20px;
    margin-top: 20px;
}

@media (max-width :1000px) {
    .explore {
        margin-top: 330px;
    }

    .input_box {
        display: grid;
        grid-template-columns: auto auto auto;
        gap: 10px;
    }

    .photos {
        display: grid;
        grid-template-columns: auto auto;
        gap: 10px;
    }

    .input_box input,
    button {
        width: 100px;
        height: 35px;
    }
    .lists li {
        margin-left: 10px;

    }
}

 @media (max-width :600px) { 
    .explore_top p{
    font-size: 15px;
    }
 }