.packages {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
}
.packages-place {
    display: grid;
    grid-template-columns: auto auto auto auto;
    margin: 20px;
    gap: 20px;
}

@media (max-width :1400px) {

    .packages-place {
        display: grid;
        grid-template-columns: auto auto auto auto;
        margin: 20px;
        gap: 20px;
    }
}
@media (max-width :1220px) {

    .packages-place {
        display: grid;
        grid-template-columns: auto auto auto ;
        margin: 20px;
        gap: 20px;
    }
}

@media (max-width :1000px) {

    .packages-place {
        display: grid;
        grid-template-columns: auto auto;
        margin: 20px;
        gap: 20px;
    }
}

@media (max-width : 600px) {
    .card_width {
        width: 15rem;
    }
}
@media (max-width : 400px) {
    .card_width {
        width: 11rem;
    }
}