.footer1 {
    margin: 0;
    padding: 0;
}

.footer {
    padding: 30px;
    display: flex;
    justify-content: space-around;
}

.footer_down {
    display: flex;
    justify-content: space-around;
    gap: 60px;
}

.icon {
    font-size: 30px;
}

.icon a {
    color: cyan;
    margin-right: 8px;
}

.company h5,
.quick h5,
.icon h5 {
    font-weight: 600;
}

.company li,
.quick li {
    list-style: none;
}

.company li a,
.quick li a {
    text-decoration: none;
    color: black;
}

.company li a:hover,
.quick li a:hover {
    text-decoration: underline;
}

hr {
    width: 300px;
}


.img h1 {
    margin-left: -40px;
    font-family: 'poppins';
}

@media (max-width:600px) {
    .footer {
        display: grid;
        gap: 15px;
    }

    .footer_down {
        display: grid;
        grid-template-columns: auto auto;
    }

    .img {
        display: flex;
        align-items: center;
        justify-content: center;
        margin-bottom: 20px;
    }

    .img h1 {
        padding-top: 10px;
        margin-left: 40px;
    }

}