.container {
    max-width: 100vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
    z-index: 100;
    position: fixed;
    background-color: black;
}

.container img {
    padding-top: 5px;

}

.container p {
    padding: 10px 0 0 25px;
    display: flex;
    font-size: 30px;
    font-family: 'poppins';
    font-weight: 600;
    gap: 20px;
}

.nav-right {
    display: flex;
    gap: 30px;
    list-style: none;
    font-size: 18px;
    font-weight: 600;
}

.nav-right li a {
    cursor: pointer;
    color: white;
    text-decoration: none;
    position: relative;
}

.nav-right li :hover {
    color: cyan;
}

.nav-right li a::after {
    content: '';
    position: absolute;
    height: 3px;
    bottom: -8px;
    width: 0;
    left: 0;
    background-color: cyan;
    transition: 0.5s;
    transform: scaleX(70%);
}

.nav-right li a:hover::after {
    width: 100%;
}

.nav-right button {
    width: 110px;
    background: linear-gradient(45deg, cyan, rgb(216, 7, 104));
    border: 2px solid black;
    border-radius: 40px;
    font-weight: 500;
    padding: 0;
}

.menu-icon i {
    position: absolute;
    font-size: 50px;
    cursor: pointer;
    display: none;
}

@media (max-width :1000px) {
    .container {
        padding: 10px;
    }

    .menu-icon i {
        display: block;
        right: 50px;
        top: 33px;
    }


    .nav-right {
        position: absolute;
        top: 88px;
        left: 0;
        width: 100%;
        height: 450px;
        background-color: aqua;
        display: flex;
        flex-direction: column;
        align-items: center;
        background-color: rgba(0, 0, 0, -1);
        backdrop-filter: blur(30px);
        font-size: 20px;
    }

    .nav-close {
        display: none;
    }

    .nav-open {
        /* margin-top: 15%; */
        display: block;
        display: flex;
        flex-direction: column;
        align-items: center;
        
    }

    .nav-right li a {
        display: block;
    }
}

@media (max-width:450px) {
    .menu-icon i {
        right: 20px;
        top: 17px;
    }
}