.contact{
    padding: 0px 0 20px 0;
}
.contact h1{
    margin:40px 0 30px 0;
}
.block{
    padding: 40px;
    background-color: #2e2c2e;
    height: auto;
    display: flex;
    justify-content: space-around;
}
.left h5, .right h5{
    color: yellow;
    font-weight: 700;
    text-transform: capitalize;
}
.left p b{
    color: rgb(27, 128, 223);
    
}

.box{
    width: 300px;
}
@media (max-width:600px) {
    .block{
        display: block;
    }
    .left{
       padding: 10px 0 0 0;
    }
}