.popular {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
}

.popular h1,
.packages h1,
.about h1,
.contact h1 {
    text-transform: uppercase;
    font-size: 50px;
    font-weight: 600;
    margin-top: 20px;
    color: cyan;
    text-shadow: 0px 0px 10px cyan;
}

.popular-places{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    margin: 50px;
    gap: 20px;
}


@media (max-width :1000px) {
    /* .popular {
        margin-top: 280px;
    } */

    .popular h1,
    .packages h1,
    .about h1 ,
    .contact h1{
        font-size: 30px;
        font-weight: 600;
        margin-top: 20px;
    }

    .popular-places {
        display: grid;
        grid-template-columns: auto auto;
        margin: 30px;
        gap: 20px;
    }
}

@media (max-width :600px) {

    .popular h1,
    .packages h1,
    .about h1,
    .contact h1 {
        margin-top: 20px;
    }

    .popular-places {
        display: grid;
        grid-template-columns: auto;
        margin: 30px;
        gap: 20px;
    }
}

