.home {
    width: 100%;
    margin: auto;
    color: black;
}

video {
    width: 100%;
    height: 100vh;
    top: 0;
    bottom: 0;
    object-fit: cover;
}

.home-content {
    margin: -380px 0 0 0;
}

.textdiv {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

}

.small-txt {
    font-size: 40px;
    font-weight: 500;
    background: linear-gradient(50deg, rgb(219, 3, 190), rgb(2, 104, 90));
    color: transparent;
    background-clip: text;
}

.home-title {
    font-size: 60px;
    font-weight: 700;
    background: linear-gradient(45deg, rgb(192, 7, 38), rgb(7, 241, 210));
    color: transparent;
    background-clip: text;
}
.input-box {
    display: flex;
    align-items: center;
    margin-top: 80px;
    font-family: Georgia, 'Times New Roman', Times, serif;
}
.input-box button{
    padding: 0;
    width: 300px;
    background-color: rgb(187, 7, 127);
    font-size: 18px;
    font-weight: 600;
   
}
.input-box button i{
    font-size: 22px;
}


@media (max-width :1000px) {
    .home-content {
        margin-top: -500px;
    }
}

@media (max-width :700px) {

    .small-txt {
        font-size: 35px;
    }

    .home-title {
        font-size: 45px;
    }
}

@media (max-width :525px) {

    .small-txt {
        font-size: 25px;
    }

    .home-title {
        font-size: 30px;
    }

}

@media (max-width:400px) {
    .home-content {
        margin-top: -500px;
    }
}


